.neos-ydi6SG_header {
  margin: 0;
  line-height: 20px;
  position: relative;
}

.neos-ydi6SG_header__chevron {
  height: 100%;
  width: 18px;
  color: #adadad  ;
  text-align: center;
  cursor: pointer;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.neos-ydi6SG_header__chevron:hover {
  color: #00adee  ;
}

.neos-ydi6SG_header__chevron--isCollapsed > svg {
  transform: translateY(3px)translateX(-2px)rotate(-90deg);
}

.neos-ydi6SG_header__chevron--isHiddenInIndex {
  opacity: .5;
}

.neos-ydi6SG_header__chevron--isLoading, .neos-ydi6SG_header__chevron--isLoading:hover {
  color: #adadad   !important;
}

.neos-ydi6SG_header__chevron--isLoading > svg {
  margin-top: 6px;
}

.neos-ydi6SG_header__iconWrapper {
  width: 2em;
  text-align: center;
  display: inline-block;
  position: absolute;
}

.neos-ydi6SG_header__data {
  min-width: 100%;
  white-space: nowrap;
  cursor: pointer;
  border-left: 2px solid rgba(0, 0, 0, 0);
  padding: .1em 0;
  display: inline-block;
  position: relative;
}

.neos-ydi6SG_header__data--isHiddenInIndex {
  opacity: .5;
}

.neos-ydi6SG_header__data--isHidden {
  text-decoration: line-through;
}

.neos-ydi6SG_header__data--isDragging {
  opacity: .2;
}

.neos-ydi6SG_header__data--isDirty {
  border-left: 2px solid #ff8700  ;
}

.neos-ydi6SG_header__data--isFocused {
  background: #323232  ;
}

.neos-ydi6SG_header__data--isFocused.neos-ydi6SG_header__data--isHiddenInIndex, .neos-ydi6SG_header__data--isFocused.neos-ydi6SG_header__data--isHidden {
  opacity: .8;
}

.neos-ydi6SG_header__data--acceptsDrop {
  background-color: #00adee  ;
}

.neos-ydi6SG_header__data--acceptsDrop *, .neos-ydi6SG_header__data--deniesDrop * {
  pointer-events: none;
}

.neos-ydi6SG_header__labelWrapper {
  max-width: 264px;
  text-overflow: ellipsis;
  vertical-align: middle;
  line-height: 1.5;
  display: inline-block;
  overflow: hidden;
}

.neos-ydi6SG_header__label {
  margin-left: 2em;
}

.neos-ydi6SG_header__data--isActive .neos-ydi6SG_header__label {
  color: #00adee  ;
}

.neos-ydi6SG_contents {
}

.neos-ydi6SG_dropTarget {
  height: 2px;
  padding-left: 15px;
  position: absolute;
  left: 0;
  right: 0;
}

.neos-ydi6SG_dropTarget--before {
  z-index: 1  ;
  padding-top: 4px;
  padding-bottom: 4px;
  top: -5px;
}

.neos-ydi6SG_dropTarget--after {
  z-index: 2  ;
  padding-top: 6px;
  padding-bottom: 0;
  bottom: 1px;
}

.neos-ydi6SG_dropTarget__inner {
  visibility: hidden;
  height: 0;
  margin-top: -8px;
}

.neos-ydi6SG_dropTarget__inner--acceptsDrop {
  z-index: 100000;
  visibility: visible;
}

.neos-ydi6SG_dropTarget__inner svg {
  fill: #00adee  ;
}
