.neos-UeYiwa_dropDown {
  width: 100%;
  display: inline-block;
  position: relative;
}

.neos-UeYiwa_dropDown__btn {
  width: 100%;
  height: 40px  ;
  text-align: left;
  color: #fff;
  cursor: pointer;
  font-size: 14px  ;
  padding: 0 16px  ;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 40px  ;
  background: #323232  ;
  border: 0;
  border-radius: 2px;
  display: block;
  overflow: hidden;
}

.neos-UeYiwa_dropDown__btn:focus {
  outline: 1px solid #00adee  ;
}

.neos-UeYiwa_dropDown__btn svg {
  pointer-events: none;
}

.neos-UeYiwa_dropDown__btn--withChevron {
  padding-right: 40px  ;
}

.neos-UeYiwa_dropDown__btnLabel {
  margin-right: .5em;
}

.neos-UeYiwa_dropDown__chevron {
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
}

.neos-UeYiwa_dropDown__contents {
  z-index: 1  ;
  width: 100%;
  background: #323232  ;
  margin: 0;
  list-style: none;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  box-shadow: 0 5px 5px rgba(0, 0, 0, .2);
}

.neos-UeYiwa_dropDown__contents--scrollable {
  position: fixed;
  top: auto;
  overflow-y: auto;
}

.neos-UeYiwa_dropDown__contents--isOpen {
  flex-direction: column;
  display: flex;
}

.neos-UeYiwa_dropDown--darker > .neos-UeYiwa_dropDown__btn--open, .neos-UeYiwa_dropDown--darker > .neos-UeYiwa_dropDown__contents {
  background-color: #222  ;
}

.neos-UeYiwa_dropDown--darkest > .neos-UeYiwa_dropDown__btn--open, .neos-UeYiwa_dropDown--darkest > .neos-UeYiwa_dropDown__contents {
  background-color: #141414  ;
}

.neos-UeYiwa_dropDown--padded > .neos-UeYiwa_dropDown__contents {
  padding: 16px  ;
}
