.neos-Nc5Y8q_listPreviewElement {
  cursor: pointer;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 14px;
  line-height: calc(40px   - 10px);
  padding: 5px 16px  ;
  overflow: hidden;
}

.neos-Nc5Y8q_listPreviewElement--isDisabled {
  color: #999  ;
  cursor: not-allowed;
}

.neos-Nc5Y8q_listPreviewElement--isHighlighted {
  background-color: #00adee   !important;
}

.neos-Nc5Y8q_listPreviewElement--isHighlighted > span {
  color: #fff;
}

.neos-Nc5Y8q_listPreviewElement__icon {
  margin-right: .5em;
}

.neos-Nc5Y8q_listPreviewElement__iconWrapper {
  width: 2em;
  text-align: center;
