.neos-LDUhKW_checkbox {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  margin-right: 8px;
  display: block;
  position: relative;
}

.neos-LDUhKW_checkbox__input {
  z-index: 1  ;
  width: 20px;
  height: 20px;
  opacity: 0;
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.neos-LDUhKW_checkbox__icon {
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}

.neos-LDUhKW_checkbox__inputMirror {
  width: 20px;
  height: 20px;
  vertical-align: top;
  background: #3f3f3f  ;
  border-radius: 2px;
  font-size: 20px;
  font-weight: bold;
  display: inline-block;
}

.neos-LDUhKW_checkbox__inputMirror--active {
  background: #00adee  ;
}

.neos-LDUhKW_checkbox__checked .neos-LDUhKW_checkbox__icon {
  opacity: 1;
}

.neos-LDUhKW_checkbox__disabled {
  opacity: .65;
  cursor: not-allowed;
}

.neos-LDUhKW_checkbox__disabled .neos-LDUhKW_checkbox__input {
  cursor: not-allowed;
}
