.neos-uZyRdq_icon {
  font: normal normal normal FontAwesome;
  font-size: 14px / 1;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
}

.neos-uZyRdq_icon--paddedLeft {
  margin-left: .75em;
}

.neos-uZyRdq_icon--paddedRight {
  margin-right: .75em;
}

.neos-uZyRdq_icon--color-warn {
  color: #ff8700  ;
}

.neos-uZyRdq_icon--color-error {
  color: #ff460d  ;
}

.neos-uZyRdq_icon--color-primaryBlue {
  color: #00adee  ;
}

.neos-uZyRdq_icon--huge svg {
  height: 3em;
}

.neos-uZyRdq_icon--large svg {
  height: 2em;
}

.neos-uZyRdq_icon--big svg {
  height: 1.33em;
}

.neos-uZyRdq_icon--small svg {
  height: 1em;
  max-height: 1em;
  max-width: 100%;
}

.neos-uZyRdq_icon--tiny svg {
  height: .75em;
}

.neos-uZyRdq_icon--resource {
  width: 100%;
  justify-content: center;
  display: inline-grid;
}
