.neos-0SRomW_textArea {
  width: 100%;
  height: 40px;
  background: #323232  ;
  resize: vertical;
  border: 0;
  border-radius: 2px;
  padding: 10px 14px;
  transition: height .3s;
}

.neos-0SRomW_textArea:focus {
  background: #fff  ;
  color: #141414  ;
  outline: 0;
}

.neos-0SRomW_textArea::-webkit-input-placeholder {
  color: #999  ;
  font-family: Noto Sans, sans-serif;
}

.neos-0SRomW_textArea--invalid {
  outline: 2px solid #ff460d   !important;
}

.neos-0SRomW_textArea--highlight {
  outline: 2px solid #00a338  ;
}

.neos-0SRomW_textArea--disabled {
  opacity: .65;
  cursor: not-allowed;
}
