.neos-2Y4dCG_panel {
  position: relative;
}

.neos-2Y4dCG_panel__headline {
  cursor: pointer;
  height: 40px  ;
  padding: 0 16px  ;
  line-height: 40px  ;
  font-weight: bold;
  font-size: 14px  ;
  margin: 0;
}

.neos-2Y4dCG_panel__headline--noPadding {
  padding: 0;
}

.neos-2Y4dCG_panel__toggleBtn {
  position: absolute;
  top: 0;
  right: 0;
}

.neos-2Y4dCG_panel__contents {
  padding: 0 16px  ;
  transition: .25s  ease padding;
}

.neos-2Y4dCG_panel--isOpen .neos-2Y4dCG_panel__contents {
  padding-top: 5px;
  padding-bottom: 5px;
}

.neos-2Y4dCG_panel--condensed.neos-2Y4dCG_panel--isOpen .neos-2Y4dCG_panel__contents {
  padding-top: 0;
  padding-bottom: 0;
}

.neos-2Y4dCG_panel__contents--noPadding {
  padding: 0;
}
