.reset {
    /*
     * note for the future you:
     * in case we sometime have problems with the specificity - we can always use :where(.reset) {} to force a low specificity
     */
    font-family: 'Noto Sans', sans-serif;
    font-weight: normal;
    color: #FFF;
    margin: 0;
    padding: 0;

    &,
    &:before,
    &:after {
        box-sizing: border-box;
    }

    /* some browsers set a font-size or margin in their user stylesheets */
    & input,
    & textarea,
    & select,
    & button {
        font-size: var(--fontSize-Base);
        margin: 0;
    }
    & ul {
        padding: 0;
    }
    /* webkit adds some weird spacing otherwise */
    & li {
        display: block;
    }
    & input::-webkit-search-cancel-button {
        display: none;
    }
    /* webkit adds white box around search box */
    & input {
        appearance: none;
    }
}
