.neos-UkeiQW_selectedOptions__item {
  width: 100%;
  background: #141414  ;
  border-bottom: 1px solid #222  ;
  border-radius: 2px;
  display: flex;
  position: relative;
}

.neos-UkeiQW_selectedOptions__item:hover .neos-UkeiQW_selectedOptions__itemIcon {
  display: none;
}

.neos-UkeiQW_selectedOptions__item:hover .neos-UkeiQW_selectedOptions__itemIcon--onHover {
  display: inline;
}

.neos-UkeiQW_selectedOptions__item--draggable {
  cursor: move;
}

.neos-UkeiQW_selectedOptions__innerPreview {
  border-right: 1px solid #222  ;
  width: 100%;
  overflow: hidden;
}

.neos-UkeiQW_selectedOptions__innerPreview > div {
  box-shadow: none !important;
}

.neos-UkeiQW_selectedOption__removeButton {
  height: auto;
  background: #323232  ;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}

.neos-UkeiQW_selectedOption__moveButton {
  cursor: move;
  height: auto;
  width: 20px;
  min-width: 20px;
  color: #3f3f3f  ;
  border-right: 1px solid #222  ;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}

.neos-UkeiQW_selectedOption__moveButton svg {
  color: #3f3f3f  ;
}

.neos-UkeiQW_selectedOption__moveButton:hover, .neos-UkeiQW_selectedOption__moveButton:focus {
  background: #3f3f3f  ;
  color: #999   !important;
}

:is(.neos-UkeiQW_selectedOption__moveButton:hover, .neos-UkeiQW_selectedOption__moveButton:focus) svg {
  color: #999   !important;
}
