.neos-3bgXga_multiLineWithThumbnail__item {
  box-sizing: content-box;
  background: #141414  ;
}

.neos-3bgXga_multiLineWithThumbnail__item--multiLine {
  line-height: 20px;
}

.neos-3bgXga_multiLineWithThumbnail__secondaryLabel, .neos-3bgXga_multiLineWithThumbnail__tertiaryLabel {
  color: #999  ;
  text-overflow: ellipsis;
  font-size: 12px;
  display: block;
  overflow: hidden;
}

.neos-3bgXga_multiLineWithThumbnail__image {
  width: calc(1.33 * (40px   + 16px  ));
  height: calc(40px   + 16px  );
  object-fit: contain;
  vertical-align: middle;
  margin-right: .75em;
  margin-left: calc(16px  * -1);
  margin-top: calc(8px  * -1);
  margin-bottom: calc(8px  * -1);
  background-color: #fff;
  background-image: linear-gradient(45deg, #ccc 25%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0) 75%, #ccc 75%, #ccc), linear-gradient(45deg, #ccc 25%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0) 75%, #ccc 75%, #ccc);
  background-position: 0 0, 25px 25px;
  background-size: 10px 10px;
  display: inline-block;
}

.neos-3bgXga_multiLineWithThumbnail__item:hover .neos-3bgXga_multiLineWithThumbnail__secondaryLabel, .neos-3bgXga_multiLineWithThumbnail__item:hover .neos-3bgXga_multiLineWithThumbnail__tertiaryLabel {
  color: #fff;
}
