.neos-U8YfbW_wrapper {
  display: inline-block;
  position: relative;
}

.neos-U8YfbW_wrapper__btn {
  width: 40px  ;
  position: relative;
}

.neos-U8YfbW_wrapper__btnModeIcon {
  color: #00adee  ;
  position: absolute;
  bottom: 4px;
  right: 4px;
}

[disabled] > .neos-U8YfbW_wrapper__btnModeIcon {
  opacity: 0;
}

.neos-U8YfbW_wrapper:hover .neos-U8YfbW_wrapper__btnIcon {
  color: #00adee  ;
}

.neos-U8YfbW_wrapper__dropDown {
  z-index: 1  ;
  background: #222  ;
  border: 1px solid #3f3f3f  ;
  border-top: 0;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
}

.neos-U8YfbW_wrapper__dropDown--isOpen {
  display: block;
}

.neos-U8YfbW_wrapper__dropDownItem {
  width: calc(40px   - 2px);
  height: calc(40px   - 2px);
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  display: block;
  position: relative;
}

.neos-U8YfbW_wrapper__dropDownItem:hover, .neos-U8YfbW_wrapper__dropDownItem:hover > * {
  color: #00adee  ;
}

.neos-U8YfbW_wrapper__dropDownItem > * {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}
