.neos-S2AdUG_tooltip {
  position: absolute;
}

.neos-S2AdUG_tooltip ul {
  margin: 0;
}

.neos-S2AdUG_tooltip--inline {
  margin-bottom: 8px  ;
  position: relative !important;
}

.neos-S2AdUG_tooltip--arrow {
  width: 0;
  height: 0;
  margin-left: calc(8px  * -1);
  border-left: 8px  solid transparent;
  border-right: 8px  solid transparent;
  border-bottom: 8px  solid #141414  ;
  position: relative;
  top: 0;
  left: 50%;
}

.neos-S2AdUG_tooltip--asError .neos-S2AdUG_tooltip--arrow {
  border-bottom: 8px  solid #ff460d  ;
}

.neos-S2AdUG_tooltip--inner {
  padding: 8px  ;
  background: #141414  ;
  color: #fff;
  position: relative;
  box-shadow: 0 0 10px rgba(125, 125, 125, .2);
}

.neos-S2AdUG_tooltip--asError .neos-S2AdUG_tooltip--inner {
  background: #ff460d  ;
}
