.neos-jr8mIG_tabs {
  height: 100%;
}

.neos-jr8mIG_tabs__content {
  overflow-y: auto;
}

.neos-jr8mIG_tabNavigation {
  background: #141414  ;
  border-bottom: 1px solid #3f3f3f  ;
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
}

.neos-jr8mIG_tabNavigation:before, .neos-jr8mIG_tabNavigation:after {
  content: "";
  display: table;
}

.neos-jr8mIG_tabNavigation:after {
  clear: both;
}

.neos-jr8mIG_tabNavigation__item {
  float: left;
  border-top: 1px solid #3f3f3f  ;
  border-right: 1px solid #3f3f3f  ;
  color: #fff  ;
  position: relative;
}

.neos-jr8mIG_tabNavigation__item--isActive {
  background-color: #222  ;
  cursor: default;
  border-bottom: 1px solid #222  ;
  margin-bottom: -1px;
}

.neos-jr8mIG_tabNavigation__item--isActive:after {
  content: " ";
  height: 2px;
  width: 100%;
  background: #00adee  ;
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
  right: 0;
}

.neos-jr8mIG_tabNavigation__item--isActive > .neos-jr8mIG_tabNavigation__itemBtn {
  color: #00adee  ;
}

.neos-jr8mIG_tabNavigation__itemBtn {
  height: 40px  ;
  padding: 0 16px  ;
  line-height: 40px  ;
  cursor: pointer;
  background: 0;
  border: 0;
  display: inline-block;
}

.neos-jr8mIG_tabNavigation__itemBtn:hover {
  color: #00adee  ;
}

.neos-jr8mIG_tabNavigation__itemBtn:focus {
  outline: 0;
}

.neos-jr8mIG_tabNavigation__itemBtnIcon {
  color: currentColor;
}

.neos-jr8mIG_tabNavigation__itemBtnIcon--hasLabel {
  margin-right: 8px  ;
}

.neos-jr8mIG_panel {
  overflow-y: auto;
}
